import { FETCH_PRODUTOS } from "../actions/actionTypes";

const INITIAL_STATE = {
  produtos: []
};

export default function productsReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case FETCH_PRODUTOS:
      return {
        ...state,
        produtos: action.products
      };
    default:
      return state;
  }
}
