import React from "react";
import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";

import MiniCart from "./carrinho/MiniCart";

import Logo from "../logo.png";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import Box from "@material-ui/core/Box";

import { fade, makeStyles } from "@material-ui/core/styles";
import MenuIcon from "@material-ui/icons/Menu";

export default function AppHeader() {
  const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1
    },
    appBar: {
      backgroundColor: theme.palette.background.paper
    },
    menuButton: {
      direction: "rtl"
    },
    title: {
      flexGrow: 1,
      display: "none",
      [theme.breakpoints.up("sm")]: {
        display: "block"
      }
    },
    search: {
      position: "relative",
      borderRadius: theme.shape.borderRadius,
      backgroundColor: fade(theme.palette.common.white, 0.15),
      "&:hover": {
        backgroundColor: fade(theme.palette.common.white, 0.25)
      },
      marginLeft: 0,
      width: "100%",
      [theme.breakpoints.up("sm")]: {
        marginLeft: theme.spacing(1),
        width: "auto"
      }
    },
    searchIcon: {
      width: theme.spacing(7),
      height: "100%",
      position: "absolute",
      pointerEvents: "none",
      display: "flex",
      alignItems: "center",
      justifyContent: "center"
    },
    inputRoot: {
      color: "inherit"
    },
    inputInput: {
      padding: theme.spacing(1, 1, 1, 7),
      transition: theme.transitions.create("width"),
      width: "100%",
      [theme.breakpoints.up("sm")]: {
        width: 120,
        "&:focus": {
          width: 200
        }
      }
    }
  }));
  const classes = useStyles();

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <div className={classes.root}>
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="default"
            aria-label="open drawer"
          >
            <div>
              <MenuIcon
                aria-controls="simple-menu"
                aria-haspopup="true"
                onClick={handleClick}
              />
              <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
                style={{ width: "100%" }}
              >
                <MenuItem onClick={handleClose}>
                  <Link component={RouterLink} to="/compre-aqui">
                    Compre Aqui
                  </Link>
                </MenuItem>
                <MenuItem onClick={handleClose}>
                  <Link component={RouterLink} to="/camarotes">
                    Camarotes
                  </Link>
                </MenuItem>
                <MenuItem onClick={handleClose}>
                  <Link component={RouterLink} to="/blocos">
                    Blocos
                  </Link>
                </MenuItem>
              </Menu>
            </div>
          </IconButton>
          <Link component={RouterLink} to="/">
            <img src={Logo} className="img-fluid Logo" alt="Folia Bahia" />
          </Link>

          <div style={{ width: "100%" }}>
            <Box
              display="flex"
              justifyContent="flex-end"
              className="text-right"
            >
              <IconButton component={RouterLink} to="/minha-conta">
                <AccountCircleIcon />
              </IconButton>
              <IconButton component={RouterLink} to="/compre-aqui">
                <MiniCart />
              </IconButton>
            </Box>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}
