import { ADD_ITEM, REM_ITEM } from "../actions/actionTypes";

const INITIAL_STATE = {
  items: [],
  total: 0
};

export default function cartReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case ADD_ITEM:
      return {
        ...state,
        addItems: state.items.push(action.item),
        total: state.total + parseFloat(action.item.price)
      };
    case REM_ITEM:
      return {
        ...state,
        addedItem: state.items.pop(),
        total: state.total - action.item.price
      };
    default:
      return state;
  }
}
