import { fetchProdutosAction } from ".";
import { WOO_API } from "../woocommerce";

export default function fetchProdutos(dispatch) {
  WOO_API.get("products/", {
    orderby: "id",
    order: "asc",
    per_page: "100"
  })
    .then(res => {
      if (res.error) {
        throw res.error;
      }
      dispatch(fetchProdutosAction(res.data));
    })
    .catch(error => {
      console.log(error);
    });
}
